html {
  font-size: 14px;
  -webkit-tap-highlight-color: transparent;
}
body,
html {
  /* // font-family: Microsoft YaHei, Helvetica Neue, Arial, sans-serif; */
  height: 100%;
  width: 100%;
  font-family: Open Sans, sans-serif;
  font-size: 14px;
  margin: 0;
  padding: 0;
}
* {
  border: 0;
  font-size: 100%;
  vertical-align: baseline;
}
a {
  text-decoration: none;
}
ul {
  list-style: none;
  padding: 0;
  margin: 0;
}
::-webkit-scrollbar {
  width: 8px;
  height: 8px;
  background-color: transparent;
}
::-webkit-scrollbar-corner {
  background-color: transparent;
}
::-webkit-scrollbar-thumb {
  background-color: #3e4046;
}
.App-router {
  width: 100%;
  height: 100%;
  /* background: #191c23; */
}
.dw-icon * {
  pointer-events: none;
}
.dw-icon {
  position: relative;
  overflow: hidden;
  display: -ms-inline-flexbox;
  display: inline-flex;
  -ms-flex-pack: center;
  justify-content: center;
  -ms-flex-align: center;
  align-items: center;
  background-repeat: no-repeat;
  background-position: 50%;
  width: 34px;
  height: 34px;
  border: 8px solid transparent;
  background-clip: content-box;
  border-radius: 0;
  background-size: 24px;
  margin: 0;
  opacity: 1;
  color: #fff;
  -webkit-transition: opacity 0.2s ease-out, background-color 0.2s ease-out, -webkit-transform 0.2s ease-out;
  transition: opacity 0.2s ease-out, background-color 0.2s ease-out, -webkit-transform 0.2s ease-out;
  -o-transition: transform 0.2s ease-out, opacity 0.2s ease-out, background-color 0.2s ease-out;
  transition: transform 0.2s ease-out, opacity 0.2s ease-out, background-color 0.2s ease-out;
  transition: transform 0.2s ease-out, opacity 0.2s ease-out, background-color 0.2s ease-out, -webkit-transform 0.2s ease-out;
  cursor: pointer;
  contain: strict;
  vertical-align: bottom;
  font-size: 20px;
}
.dw-icon-inner {
  font-family: SFwKOoXDmT3wTI5PGKmj3AeF, sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  line-height: 1;
  text-transform: none;
  letter-spacing: normal;
  word-wrap: normal;
  white-space: nowrap;
  direction: ltr;
  -webkit-transition: inherit;
  -o-transition: inherit;
  transition: inherit;
  -webkit-font-smoothing: antialiased;
  text-rendering: optimizeLegibility;
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-feature-settings: "liga";
  font-feature-settings: "liga";
  pointer-events: none;
}
.dw-icon-active {
  opacity: 1;
  color: #000;
  background-color: rgba(255, 255, 255, 0.9);
}
/* // .tool-menu i {
